*,
::before,
::after
{
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	list-style: none;
	outline: none;
	vertical-align: baseline;
}

button
{
	display: inline-block;
	border: none;
	background-color: transparent;
	cursor: pointer;

	font-family: inherit;
	color: inherit;
}

input
{
	font-family: inherit;
	color: inherit;
}

table
{
	border-collapse: collapse;
	border-spacing: 0;
}

a
{
	cursor: pointer;
	color: inherit;
	text-decoration: none;

	&:visited
	{
		color: inherit;
		text-decoration: inherit;
	}
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video
{
	margin: 0;
	padding: 0;
	border: 0;
	font: inherit;
	vertical-align: baseline;
}
